/* global graphql */

const _ = require('lodash');
import React from 'react';
import Link from 'gatsby-link'
import Breadcrumb from '../components/breadcrumb';
import '../components/button/_button.scss';


class SupportPage extends React.Component {
	constructor(props) {
		super(props);

	}



	render() {
		return (
			<main className="container-fluid">
				<Breadcrumb title="Support the site" />
				<div className="row">
					<div className="col-md-10 col">
						<p>
							Any support to help cover the server/coding as well as help me keep on adding more features like: 
						</p>
						<ul>
							<li>Season long Records</li>
							<li>Player Records</li>
							<li>Rosters</li>
							<li>Full Game logs</li>
							<li>Draft Recaps</li>
							<li>Power Rankings</li>
							<li>Playoff Odds</li>
							<li>Designs/Skins</li>
							<li>Compatibility with other Fantasy Providers</li>
						</ul>
						<p>
							To see the full list with all the details go to the <Link to="/roadmap/" >Roadmap page</Link> 
						</p>
						<p>
							I'm also a Freelance Web Developer you can check out my work at <a href="https://squidsource.com/" target="_blank">https://squidsource.com/</a>
						</p>
						<p>
							If you have an interest in helping support the site, please use Paypal, Buy me a coffee or Patreon buttons.<br />
						</p>
							<a className="bmc-button my-3" target="_blank" href="https://www.paypal.com/paypalme2/FabianBrenes">
								<img className="mr-2" src="https://www.paypalobjects.com/webstatic/icon/pp258.png" width="40" alt="Paypal.me" />
								<span>Paypal Me</span>
							</a>
						
							<a className="bmc-button my-3" target="_blank" href="https://www.buymeacoffee.com/muslito">
								<img className="mr-2" src="https://cdn.buymeacoffee.com/buttons/bmc-new-btn-logo.svg" alt=""/>
								<span>Buy me a coffee</span>
							</a>

							<a className="bmc-button my-3" target="_blank" href="https://www.patreon.com/bePatron?u=13567542" data-patreon-widget-type="become-patron-button">
								<img className="mr-2" src="/img/patreon.jpg" width="40" alt="Patreon" />
								Become a Patron!
							</a>
							
						
					</div>
				</div>

			</main>);
	}
}
export default SupportPage;





